<template>
  <f7-page>
    <f7-navbar title="Aborto" back-link="Volver">
      <f7-nav-right>
        <f7-link icon-if-ios="f7:menu" icon-if-md="material:menu" panel-open="right"></f7-link>
      </f7-nav-right>
    </f7-navbar>
    <f7-block-title>Aborto</f7-block-title>

    <f7-block strong>
      <div class="list accordion-list">
        <ul>
          <li class="accordion-item" v-for="(value, key) in cataborto" :key="value.id">
            <a href="#" class="item-link">
              <div class="item-content">
                <div class="item-inner">
                  <div class="item-title">{{ value.title.rendered }}</div>
                </div>
              </div>
            </a>
            <div class="accordion-item-content">
              <div class="block">
                <nl2br tag="p" :text="value.acf.respuestas" />
              </div>
            </div>
          </li>
        </ul>
      </div>
    </f7-block>
  </f7-page>
</template>

<script>
import axios from 'axios'
import Nl2br from 'vue-nl2br'
export default {
  name: 'datoscataborto',
  components: {
    Nl2br,
  },
  data: () => ({
    cataborto: [],
    errors: []
  }),

  created () {
    axios.get('//faq.viplecon.com/wp-json/wp/v2/preguntas_frecuentes?tags=39&per_page=100')
      .then(response => {
        this.cataborto = response.data
        console.log(response)         // This will give you access to the full object
      })
      .catch(e => {
        this.errors.push(e)
      })
  }
}
</script>



